import React from "react";

function Contact(props) {
  return (
    <div className="contact">
      <h3>
        <img
          src="../images/minibox.png"
          alt=""
          style={{ height: "25px", marginRight: "5px" }}
        />
        &nbsp;Contact
      </h3>

      <div>
        <span>
          <i className="fa fa-envelope"></i>
        </span>
        <a href="mailto:draftboxdesign@gmail.com">draftboxdesign@gmail.com</a>
      </div>
      <div>
        <span>
          <i className="fa fa-facebook-square"></i>
        </span>
        <a
          href="https://www.facebook.com/draftboxdesign"
          target="_blank"
          rel="noreferrer"
        >
          fb.com/draftboxdesign
        </a>
      </div>
      <div>
        <span>
          <i className="fa fa-rocket"></i>
        </span>
        <span>more to come...</span>
      </div>
    </div>
  );
}

export default Contact;
