import { useState } from "react";
import "./App.scss";
import About from "./components/About";
import Contact from "./components/Contact";
import Portfolio from "./components/Portfolio";
import Profile from "./components/Profile";

function App() {
  const [openMenu, setOpenMenu] = useState(false);
  const [page, setPage] = useState("Home");

  function toggleMenu() {
    setOpenMenu(!openMenu);
  }

  function changePage(page = "Home") {
    setPage(page);
    setOpenMenu(false);
    document.title = "Draftbox Design" + (page === "Home" ? "" : ` / ${page}`);
  }

  return (
    <>
      <div className="container" id="container">
        <div className={openMenu ? "openMenu" : ""}>
          <img
            src="../images/draftbox.svg"
            alt="Draftbox Logo"
            style={{ width: "100px" }}
          />
          <div>
            <ul>
              <li
                onClick={() => changePage("Home")}
                className={page === "Home" ? "active" : ""}
              >
                Home
              </li>
              <li
                onClick={() => changePage("Portfolio")}
                className={page === "Portfolio" ? "active" : ""}
              >
                Portfolio
              </li>
              <li
                onClick={() => changePage("Contact")}
                className={page === "Contact" ? "active" : ""}
              >
                Contact
              </li>
              <li
                onClick={() => changePage("About")}
                className={page === "About" ? "active" : ""}
              >
                About
              </li>
            </ul>
          </div>

          <div>
            <ul style={{ padding: "6px 0" }}></ul>
          </div>

          <div>
            <ul style={{ padding: "2px 0" }}></ul>
          </div>
        </div>

        <div>
          {page === "Home" ? <Profile></Profile> : ""}
          {page === "Portfolio" ? <Portfolio></Portfolio> : ""}
          {page === "Contact" ? <Contact></Contact> : ""}
          {page === "About" ? <About></About> : ""}

          <section className="footer">Draftbox Design &copy; 2021</section>
        </div>
      </div>

      <div id="svgLogoAnim">
        <div id="svgImgs">
          <img id="petal3" src="../images/petal3.svg" alt="" />
          <img id="petal2" src="../images/petal2.svg" alt="" />
          <img id="petal1" src="../images/petal1.svg" alt="" />
          <img id="box3" src="../images/box3.svg" alt="" />
          <img id="box2" src="../images/box2.svg" alt="" />
          <img id="box1" src="../images/box1.svg" alt="" />
          <div id="logoContainer">
            <div id="logoLayer1">
              <div id="logoLayer2"></div>
            </div>
          </div>
        </div>
      </div>

      <section
        onClick={toggleMenu}
        className={"mobileOverlay " + (openMenu ? "openMenu" : "")}
      ></section>

      <section
        onClick={toggleMenu}
        className={"mobileMenu " + (openMenu ? "openMenu" : "")}
      >
        <img src="../images/minibox.png" alt="Draftbox Logo" />
      </section>
    </>
  );
}

export default App;
